import React from 'react'
import Lab from './components/Lab'

const LabsList = ({ labs, styles }) => {
  const totalLabs = labs.length
  return (
    <div className={styles}>
      {labs.map((lab, index) => {
        return <Lab number={totalLabs - index} time={lab} />
      })}
    </div>
  )
}

export default LabsList
